@forward "uswds-theme";
@forward "uswds";
@forward "uswds-theme-custom-styles";
@forward "uswds-utilities";

html {
    scroll-behavior: smooth;
}

// COLORS

:root {
    --primary-light: #bcd9ed;
    --primary: #336a90;
    --primary-dark: #264a64;
    --secondary-lighter: #fef5e4;
    --secondary-light: #f6f3ee;
    --secondary: #cfc3ae;
    --secondary-dark: #2d261a;
    --neutral-lightest: #f0f0f0;
    --neutral-lighter: #a9aeb1;
    --neutral-light: #6b6e70;
    --neutral: #71767a;
    --neutral-dark: #1c1c1c;
    --feedback-info-light: #e7f6f8;
    --feedback-info: #009ec1;
    --feedback-info-dark: #2e6276;
    --feedback-error-light: #f4e3db;
    --feedback-error: #b50909;
    --feedback-error-dark: #6f3331;
    --feedback-warning-light: #faf3d1;
    --feedback-warning: #e5a000;
    --feedback-warning-dark: #936f38;
    --feedback-success-light: #ecf3ec;
    --feedback-success: #4d8055;
    --feedback-success-dark: #446443;
    --feedback-disabled-light: #e6e6e6;
    --feedback-disabled: #c9c9c9;
    --feedback-disabled-dark: #adadad;
    --data-viz-primary-1: #754295;
    --data-viz-primary-2: #90be6d;
    --data-viz-primary-3: #d67625;
    --data-viz-primary-4: #264a64;
    --data-viz-primary-5: #c07b96;
    --data-viz-primary-6: #3a835b;
    --data-viz-primary-7: #6f3331;
    --data-viz-primary-8: #e5a000;
    --data-viz-primary-9: #b50909;
    --data-viz-primary-10: #a1d0be;
    --data-viz-primary-11: #336a90;
    --data-viz-secondary-20: #534c9c;
    --data-viz-secondary-23: #8b4687;
    --data-viz-secondary-26: #b6406c;
}

$themeColors: (
    "primary-light": var(--primary-light),
    "primary": var(--primary),
    "primary-dark": var(--primary-dark),
    "secondary-lighter": var(--secondary-lighter),
    "secondary-light": var(--secondary-light),
    "secondary": var(--secondary),
    "secondary-dark": var(--secondary-dark),
    "neutral-lightest": var(--neutral-lightest),
    "neutral-lighter": var(--neutral-lighter),
    "neutral-light": var(--neutral-light),
    "neutral": var(--neutral),
    "neutral-warm": var(--neutral-warm),
    "feedback-info-light": var(--feedback-info-light),
    "feedback-info": var(--feedback-info),
    "feedback-info-dark": var(--feedback-info-dark),
    "feedback-error-light": var(--feedback-error-light),
    "feedback-error": var(--feedback-error),
    "feedback-error-dark": var(--feedback-error-dark),
    "feedback-warning-light": var(--feedback-warning-light),
    "feedback-warning": var(--feedback-warning),
    "feedback-warning-dark": var(--feedback-warning-dark),
    "feedback-success-light": var(--feedback-success-light),
    "feedback-success": var(--feedback-success),
    "feedback-success-dark": var(--feedback-success-dark),
    "feedback-disabled-light": var(--feedback-disabled-light),
    "feedback-disabled": var(--feedback-disabled),
    "feedback-disabled-dark": var(--feedback-disabled-dark),
    "data-viz-primary-1": var(--data-viz-primary-1),
    "data-viz-primary-2": var(--data-viz-primary-2),
    "data-viz-primary-3": var(--data-viz-primary-3),
    "data-viz-primary-4": var(--data-viz-primary-4),
    "data-viz-primary-5": var(--data-viz-primary-5),
    "data-viz-primary-6": var(--data-viz-primary-6),
    "data-viz-primary-7": var(--data-viz-primary-7),
    "data-viz-primary-8": var(--data-viz-primary-8),
    "data-viz-primary-9": var(--data-viz-primary-9),
    "data-viz-primary-10": var(--data-viz-primary-10),
    "data-viz-primary-11": var(--data-viz-primary-11),
    "data-viz-secondary-20": var(--data-viz-secondary-20),
    "data-viz-secondary-23": var(--data-viz-secondary-23),
    "data-viz-secondary-26": var(--data-viz-secondary-26),
);

@each $name, $color in $themeColors {
    .bg-brand-#{$name} {
        background-color: $color !important;
    }
    .text-brand-#{$name} {
        color: $color !important;
    }
}

//FONTS
.font-12px {
    font-size: 0.75rem;
}

.fake-bold {
    text-shadow: 0 0 0.025rem;
}

// HELPERS
.width-fit-content {
    width: fit-content;
}

// SCROLLBAR
.scroll-container {
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: hsl(0 0% 50%);
}
